<template>
  <div>
    <ul>
      <li>
        <div class="inputflex">
          <label></label>
          <el-input v-model.lazy.trim="decoinfo.name"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label></label>
          <el-select v-model.lazy="decoinfo.cate" placeholder="">
            <el-option
              v-for="item in cate_opt"
              :key="item.label"
              :label="item.label"
              :value="item.label"
            ></el-option>
          </el-select>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label></label>
          <el-input v-model.number.lazy.trim="decoinfo.cb"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label></label>
          <el-input v-model.number.lazy.trim="decoinfo.js"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label></label>
          <el-input v-model.number.lazy.trim="decoinfo.scode"></el-input>
        </div>
      </li>
      <li>
        <div class="inputflex">
          <label></label>
          <el-input v-model.lazy.trim="decoinfo.classcode"></el-input>
        </div>
      </li>
      <li>
        <el-button type="primary" @click="doadddeco" icon="el-icon-edit-outline"></el-button>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      cate: "",
      cate_opt: [
        { id: "001", label: "-" },
        { id: "002", label: "" },
        { id: "003", label: "" },
        { id: "004", label: "" }
      ],
      decoinfo: {
        js: 0,
        cb: 0,
        name: "",
        err_info: [],
        scode: "",
        classcode: "",
        decoid: ""
      }
    };
  },
  methods: {
    input_err() {
      this.err_info = []; //

      if (this.decoinfo.name.length == 0) this.err_info.push("");
      if (this.decoinfo.scode.length == 0) this.err_info.push("");
      if (!/\d+/.test(this.decoinfo.js)) this.err_info.push("");
      if (!/\d+/.test(this.decoinfo.cb)) this.err_info.push("");

      if (this.err_info.length == 0) {
        //
        return false;
      } else {
        return true;
      }
    },
    doupdatadeco() {
      alert(this.input_err());
    },
    doadddeco() {
      if (!this.input_err()) {
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: ""
        }).then(() => {
          axios({
            url: require("@/config").axios.adddeco,
            method: "post",
            data: {
              name: this.decoinfo.name,
              scode: this.decoinfo.scode,
              cb: this.decoinfo.cb,
              js: this.decoinfo.js,
              cate: this.decoinfo.cate,
              classcode: this.decoinfo.classcode,
              token:sessionStorage.getItem("token")
            }
          }).then(re => {
            this.$alert("", "", {
              confirmButtonText: ""
            }).then(() => {
              this.decoinfo.name = "";
              this.decoinfo.scode = "";
              this.decoinfo.cb = 0;
              this.decoinfo.js = 0;
              this.decoinfo.cate = "";
              this.decoinfo.classcode = "";
            });
          });
        });
        //
      } else {
        this.$message(this.err_info.join(""));
      }
    }
  }
};
</script>

<style scoped>
div button {
  width: 100%;
}
div ul li {
  margin-bottom: 10px;
}
</style>