<template>
  <div>
    <el-row>
      <el-col span="4">
        <el-button v-if="isAdd" type="success" icon="el-icon-plus" @click="doAdd"></el-button>
      </el-col>
    </el-row>
    <div v-if="!isAdd">
      <el-row gutter="10">
        <el-col span="16">
          <el-select
            v-model="select_deco"
            filterable
            remote
            reserve-keyword
            placeholder=""
            :remote-method="remoteMethod"
            :loading="queryloading"
            @change="select_item"
          >
            <el-option v-for="item in decolist" :key="item._id" :label="item.name" :value="item"></el-option>
          </el-select>
        </el-col>
        <el-col span="8">
          <el-input-number
            v-model="deco_num"
            @change="handleChange"
            :min="1"
            :max="10"
            label=""
          ></el-input-number>
        </el-col>
      </el-row>
      <el-table :data="decos" @selection-change="handleSelectionChange" ref="multipleTable">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="name" label="" width="180"></el-table-column>
        <el-table-column prop="count" label="" width="50"></el-table-column>
        <el-table-column fixed="right" label="" width="50">
          <template slot-scope="scope">
            <el-button @click="remove_deco(scope.row.name)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-divider></el-divider>
      <el-row>
        <el-col span="12" offset="12">
          <el-button-group>
            <el-button type="primary" @click="doupdate" icon="el-icon-edit"></el-button>
            <el-button type="info" icon="el-icon-refresh" @click="toggle"></el-button>
          </el-button-group>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      needAdd: false,
      deco_num: 1,
      select_deco: {},
      queryloading: false,
      isAdd: true,
      deco_id: 0,
      decos: [],
      sel_decos: [],
      deco_keys: "",
      decolist: [],
      deco_order_id: 0,
      order_id: 0,
      token: sessionStorage.getItem("token")
    };
  },
  methods: {
    toggle() {
      this.decos.forEach(d => {
        this.$refs.multipleTable.toggleRowSelection(d);
      });
    },
    handleSelectionChange(d) {
      this.sel_decos = d;
    },
    updateData(order_id) {
      this.order_id = order_id;
      axios({
        url: require("@/config").axios.orderinfo,
        method: "post",
        data: { car_order_id: this.order_id, token: this.token }
      }).then(re => {
        if (re.data.data.deco_order_id == 0) {
          //
          this.isAdd = true;
          this.decos = [];
        } else {
          this.deco_order_id = re.data.data.deco_order_id;
          this.isAdd = false;
          this.needAdd = false;
          this.decos = re.data.data.decoinfo;
        }
      });
    },
    doupdate() {
      this.$confirm("", {
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        let data = { data: this.decos };
        data.needAdd = this.needAdd;
        if (this.needAdd == true) {
          data.deco_order_id = this.deco_id;
        } else {
          data.deco_order_id = this.deco_order_id;
        }
        data.order_id = this.order_id;
        data.token = this.token;
        axios({
          url: require("@/config").axios.orderupdatedeco,
          method: "post",
          data: data
        }).then(re => {
          if (re.data.code == 200) {
            this.$message("");
            this.needAdd = false; //  
          } else {
            this.$message("");
          }
        });
      });
    },
    remove_deco(d) {
      this.decos.forEach((item, i) => {
        if (item.name == d) {
          this.decos.splice(i, 1);
        }
      });
    },
    select_item() {
      let dupData = false;
      this.decos.forEach((item, i) => {
        if (item.name == this.select_deco.name) {
          //
          dupData = true;
        }
      });

      if (dupData) {
        this.$message("");
      } else {
        this.select_deco.count = this.deco_num;
        this.decos.push(this.select_deco);
      }
    },
    remoteMethod(keys) {
      if (keys.length != "") {
        // 
        this.queryloading = true;
        axios({
          url: require("@/config").axios.decolist,
          method: "post",
          data: {
            querystr: keys,
            onlyactive: true,
            token: this.token
          }
        }).then(re => {
          this.decolist = re.data.data;
          this.queryloading = false;
        });
      } else {
        this.decolist = [];
      }
    },
    doAdd() {
      this.deco_id = Date.now(); //ID
      this.isAdd = false; //
      this.needAdd = true; //truefalse 
    }
  }
};
</script>

