<template>
  <div>
    <el-row>
      <el-col span="8" offset="8">
        <ul class="regul">
          <li>
            <el-input
              v-model="reginfo.user_id"
              placeholder=""
              prefix-icon="el-icon-user-solid"
            ></el-input>
          </li>
          <li>
            <el-input
              v-model="reginfo.user_email"
              placeholder=""
              prefix-icon="el-icon-user-solid"
            ></el-input>
          </li>
          <li>
            <el-input v-model="reginfo.user_key" placeholder="" prefix-icon="el-icon-s-tools"></el-input>
          </li>
          <li>
            <el-button @click="doreg" type="primary" icon="el-icon-edit"></el-button>
          </li>
        </ul>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      reginfo: {
        user_id: "",
        user_key: ""
      },
      reg_loading: false,
      input_is_err: true,
      id_err: "",
      key_err: ""
    };
  },
  methods: {
    input_is_ok() {
      this.input_is_err = false;
      if (this.reginfo.user_id.length == 0) {
        this.$message("");
        this.input_is_err = true;
      }

      if (this.reginfo.user_key.length < 6) {
        this.$message("6");
        this.input_is_err = true;
      } else {
        if (/[^\w\.\/]/gi.test(this.reginfo.user_key)) {
          //   . /
          this.$message("");
          this.input_is_err = true;
        }
      }
    },
    doreg() {
      this.input_is_ok();
      if (!this.input_is_err) {
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(() => {
          this.reg_loading = true;
          axios({
            url: require("@/config").axios.reg,
            method: "post",
            data: {
              name: this.reginfo.user_id,
              key: this.reginfo.user_key,
              token: sessionStorage.getItem("token")
            }
          }).then(re => {
            this.$alert(`ID ${re.data.uid}`, "", {
              confirmButtonText: "",
              callback: () => {
                this.$router.push("/");
              }
            });
          });
        });
      }
    }
  }
};
</script>

<style scoped>
div button {
  width: 100%;
}

.regul li {
  padding: 8px 0;
}
</style>